<script>
import echarts from "echarts";
import ol from "openlayers";
import icon_station from "../../../assets/img/loc.png";

var map, stationLayer; //地图、站点图层
var stationPop, selectedstationPop; //站点弹出框,左侧选中某一项时地图上打的点
var ol_center_ll = [12635198.426914247, 2543848.1879067607];

export default {
  data() {
    return {
      divheight: "", //地图所在容器的高度
      searchtext: "", //搜索字段
      selectedresourcename: "光伏", //选中的资源
      resourcecount: 0, //选中的资源项的总数量
      selectedresource: "gf", //选中的资源项的名称
      showall: false, //是否显示全部全部列表
      gflist: [], //光伏资源列表
      fdlist: [], //风电资源列表
      hdlist: [], //火电资源列表
      sdlist: [], //水电资源列表
      alltypeselectedgf: true, //所有资源列表默认显示
      alltypeselectedfd: true, //所有资源列表默认显示
      alltypeselectedhd: true, //所有资源列表默认显示
      alltypeselectedsd: true, //所有资源列表默认显示
      slidershow: true, //右侧是否显示
      joininvertercount: 0, //接入逆变器个数
      invertercapacity: 0, //逆变器容量
      cndate: "", //产能统计时间
      clickedpoint: {
        //点击的点信息
        name: "",
        stationpower: "",
        daystations: "",
        stationpr: "",
        equaltime: "",
        unconfirm: "",
        undeal: "",
        dealing: ""
      },
      selectedpointinfo: {} //左侧选中的点的信息
    };
  },
  components: {},
  methods: {
    initMap() {
      //初始化地图
      var that = this;
      map = new ol.Map({
        target: "formapbox",
        layers: [
          new ol.layer.Tile({
            source: new ol.source.OSM()
          })
        ],
        view: new ol.View({
          maxZoom: 22,
          minZoom: 1,
          zoomFactor: 2,
          center: ol_center_ll,
          zoom: 12
        })
      });

      stationLayer = new ol.layer.Vector({
        //初始化站点图层
        source: new ol.source.Vector(),
        zIndex: 12,
        style: function(feature) {
          var coordinates = feature.getGeometry().getCoordinates();
          var styles = new ol.style.Style({
            geometry: new ol.geom.Point(coordinates),
            image: new ol.style.Icon({
              src: icon_station,
              anchorXUnits: "pixels",
              anchorYUnits: "pixels",
              anchor: [10, 10]
            })
          });
          return styles;
        }
      });
      map.addLayer(stationLayer);

      //电站弹出框
      stationPop = new ol.Overlay({
        element: document.getElementById("station_Pop"),
        stopEvent: true
      });
      map.addOverlay(stationPop);

      selectedstationPop = new ol.Overlay({
        element: document.getElementById("selectedpoint"),
        stopEvent: true
      });
      map.addOverlay(selectedstationPop);

      map.on("click", function(evt) {
        //地图点击事件
        var feature = map.forEachFeatureAtPixel(
          map.getEventPixel(evt.originalEvent),
          function(feature, layer) {
            if (layer == stationLayer) {
              return feature;
            }
          }
        );
        stationPop.setPosition(undefined);
        if (feature) {
          var lon = feature.get("lon");
          var lat = feature.get("lat");

          var point = ol.proj.transform(
            [Number(lon), Number(lat)],
            "EPSG:4326",
            "EPSG:3857"
          );

          that.clickedpoint = {
            //点击的点信息
            type: feature.get("type"),
            name: feature.get("name"),
            stationid: feature.get("stationid"),
            stationpower: feature.get("stationpower"),
            daystations: feature.get("daystations"),
            stationpr: feature.get("stationpr"),
            equaltime: feature.get("equaltime"),
            unconfirm: feature.get("unconfirm"),
            undeal: feature.get("undeal"),
            dealing: feature.get("dealing")
          };

          stationPop.setPosition(point);
        }
      });
    },
    zoomOut() {
      //放大
      var zoom = map.getView().getZoom();
      map.getView().setZoom(zoom + 1);
    },
    zoomIn() {
      //缩小
      var zoom = map.getView().getZoom();
      map.getView().setZoom(zoom - 1);
    },
    resetMapViewr() {
      //视野复位
      map.getView().setCenter(ol_center_ll);
      map.getView().setZoom(12);
    },
    changeresourceitem(itemname) {
      //切换资源项
      var that = this;
      this.selectedresource = itemname;
      var flag = true;
      switch (that.selectedresource) {
        case "gf":
          that.selectedresourcename = "光伏";
          if (that.gflist.length > 0) flag = false;
          that.resourcecount = that.gflist.length;
          break;
        case "fd":
          that.selectedresourcename = "风电";
          if (that.fdlist.length > 0) flag = false;
          that.resourcecount = that.fdlist.length;
          break;
        case "hd":
          that.selectedresourcename = "火电";
          if (that.hdlist.length > 0) flag = false;
          that.resourcecount = that.hdlist.length;
          break;
        case "sd":
          that.selectedresourcename = "水电";
          if (that.sdlist.length > 0) flag = false;
          that.resourcecount = that.sdlist.length;
          break;
        default:
          that.selectedresourcename = "光伏";
          if (that.gflist.length > 0) flag = false;
          that.resourcecount = that.gflist.length;
          break;
      }
      if (flag == true) {
        this.getresourcelist();
      }
    },
    showlist(flag) {
      //展开资源列表
      this.showall = flag;
    },
    showalltype(name, flag) {
      //展开所有资源列表
      if (!name) {
        this.selectedresource = "all"; //原来的分类列表隐藏
      } else {
        this[name] = flag;
      }
    },
    sliderclick() {
      this.slidershow = !this.slidershow;
    },
    getresourcelist(type) {
      //根据资源类型获取数据
      var that = this;
      var params = {
        type: typeof type != "undefined" ? type : that.selectedresource
      };

      that.$http
        .post(
          this.BASE_INFO.BASE_URL + "api/resource/getresourcelistBytpe",
          params
        )
        .then(function(result) {
          result = result.data;
          if (result.code == 0) {
            if (result.data) {
              var data = result.data;
              switch (that.selectedresource) {
                case "gf":
                  that.gflist = data;
                  that.resourcecount = data.length;
                  break;
                case "fd":
                  that.fdlist = data;
                  that.resourcecount = data.length;
                  break;
                case "hd":
                  that.hdlist = data;
                  that.resourcecount = data.length;
                  break;
                case "sd":
                  that.sdlist = data;
                  that.resourcecount = data.length;
                  break;
                default:
                  that.gflist = data;
                  that.resourcecount = data.length;
                  break;
              }
              that.initGeoLayer(data);
            }
          }
        });
    },
    getbarstatis() {
      //获取产能统计
      var that = this;

      that.$http
        .get(this.BASE_INFO.BASE_URL + "api/resource/getcapacitystatis")
        .then(function(result) {
          result = result.data;
          if (result.code == 0) {
            if (result.data) {
              var data = result.data;
              that.initbar(data);
            }
          }
        });
    },
    getpiedata() {
      //获取两个饼状图的数据
      var that = this;

      that.$http
        .get(this.BASE_INFO.BASE_URL + "api/resource/getstationinverterpiedata")
        .then(function(result) {
          result = result.data;
          if (result.code == 0) {
            if (result.data) {
              var data = result.data;
              that.initdzstatus(data.powerstation);
              that.initnbqstatus(data.inverter);
            }
          }
        });
    },
    initbar(data) {
      //产能柱状图

      var xdata = [
        "1时",
        "2时",
        "3时",
        "4时",
        "5时",
        "6时",
        "7时",
        "8时",
        "9时",
        "10时",
        "11时",
        "12时",
        "13时",
        "14时",
        "15时",
        "16时",
        "17时",
        "18时",
        "19时",
        "20时",
        "21时",
        "22时",
        "23时",
        "24时"
      ];
      //var data=[100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100,100];
      var option = {
        color: ["#3398DB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "2px",
          right: "2px",
          top: "7px",
          bottom: "3px",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: xdata,
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            barWidth: "60%",
            data: data
          }
        ]
      };
      var myChart = echarts.init(document.getElementById("cncharts"));
      myChart.setOption(option);
    },
    initdzstatus(data) {
      //电站状态
      // var names=['正常','异常','停机','离线'];
      // var data=[
      //     {value:335, name:'正常'},
      //     {value:310, name:'异常'},
      //     {value:234, name:'停机'},
      //     {value:135, name:'离线'}
      // ]

      var names = [];

      for (var i = 0; i < data.length; i++) {
        names.push(data[i].name);
      }

      var option = {
        legend: {
          orient: "vertical",
          x: "right",
          data: names
        },
        title: {
          text: "",
          textStyle: {
            color: "#0178D7",
            fontSize: 14,
            fontWeight: "normal"
          },
          x: "center" //title居中
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["80%", "60%"], //第一个圆的大小，第二个环的直径
            avoidLabelOverlap: false,
            center: ["33%", "50%"], //调整整个图层是否是居中显示
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: data
          }
        ]
      };

      var myChart = echarts.init(document.getElementById("zdcharts"));
      myChart.setOption(option);
    },
    initnbqstatus(data) {
      //逆变器运行状态
      var names = [];

      for (var i = 0; i < data.length; i++) {
        names.push(data[i].name);
      }

      var option = {
        legend: {
          orient: "vertical",
          x: "right",
          data: names
        },
        title: {
          text: "",
          textStyle: {
            color: "#0178D7",
            fontSize: 14,
            fontWeight: "normal"
          },
          x: "center" //title居中
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["80%", "60%"], //第一个圆的大小，第二个环的直径
            avoidLabelOverlap: false,
            center: ["33%", "50%"], //调整整个图层是否是居中显示
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: data
          }
        ]
      };
      var myChart = echarts.init(document.getElementById("nbqcharts"));
      myChart.setOption(option);
    },
    getinverterinfo() {
      //获取逆变器基本信息
      var that = this;

      that.$http
        .get(this.BASE_INFO.BASE_URL + "api/resource/getinverterinfo")
        .then(function(result) {
          result = result.data;
          if (result.code == 0) {
            if (result.data) {
              var data = result.data;
              that.joininvertercount = data.joininvertercount;
              that.invertercapacity = data.invertercapacity;
            }
          }
        });
    },
    changedate() {
      this.getbarstatis();
    },

    initGeoLayer(list) {
      //打点
      // stationLayer.getSource().clear();
      for (var i = 0; i < list.length; i++) {
        var obj = list[i];
        var ftr = new ol.Feature({
          geometry: new ol.geom.Point(
            ol.proj.transform(
              [Number(obj.lon), Number(obj.lat)],
              "EPSG:4326",
              "EPSG:3857"
            )
          ),
          name: obj.name,
          stationid: obj.stationid,
          stationpower: obj.stationpower,
          daystations: obj.daystations,
          stationpr: obj.stationpr,
          equaltime: obj.equaltime,
          unconfirm: obj.unconfirm,
          undeal: obj.undeal,
          dealing: obj.dealing,
          lon: obj.lon,
          lat: obj.lat
        });
        stationLayer.getSource().addFeature(ftr);
      }
    },
    clickli(item) {
      //点击资源列表项，地图中心点定位到对应的点
      var lon = item.lon;
      var lat = item.lat;
      this.selectedpointinfo = item;

      var point = ol.proj.transform(
        [Number(lon), Number(lat)],
        "EPSG:4326",
        "EPSG:3857"
      );
      selectedstationPop.setPosition(point);
      map.getView().setCenter(point);
    },
    selectedpointclick() {
      var that = this;
      var point = ol.proj.transform(
        [
          Number(that.selectedpointinfo.lon),
          Number(that.selectedpointinfo.lat)
        ],
        "EPSG:4326",
        "EPSG:3857"
      );

      that.clickedpoint = {
        //点击的点信息
        type: that.selectedpointinfo.type,
        name: that.selectedpointinfo.name,
        stationid: that.selectedpointinfo.stationid,
        stationpower: that.selectedpointinfo.stationpower,
        daystations: that.selectedpointinfo.daystations,
        stationpr: that.selectedpointinfo.stationpr,
        equaltime: that.selectedpointinfo.equaltime,
        unconfirm: that.selectedpointinfo.unconfirm,
        undeal: that.selectedpointinfo.undeal,
        dealing: that.selectedpointinfo.dealing
      };

      stationPop.setPosition(point);
    },
    todetailPage(item) {
      //跳到详情页

      console.log("跳到详情页");
      console.dir(item);
      var doma = document.getElementById("alink");
      doma.href =
        "/resource/runanalysisdetail?name=" +
        item.name +
        "&stationid=" +
        item.stationid; //station的名称和id

      if (document.all) {
        // For IE
        doma.click();
      } else if (document.createEvent) {
        //FOR DOM2
        var ev = document.createEvent("MouseEvents");
        ev.initEvent("click", false, true);
        doma.dispatchEvent(ev);
      }
    },
    hasClass(element, cName) {
      //检查class是否存在

      return !!element.className.match(
        new RegExp("(\\s|^)" + cName + "(\\s|$)")
      );
    }
  },
  mounted() {
    //能耗日历选择月份列表
    var that = this;
    var d = new Date();
    var year = d.getFullYear();
    var month = d.getMonth() + 1;
    var temparr = [];
    var strym = year + "-" + (month < 10 ? "0" + month : month);

    var h =
      window.innerHeight ||
      document.body.clientHeight ||
      document.documentElement.clientHeight;
    h = h - 60;
    this.divheight = h + "px";

    var wrapdom = document.getElementsByClassName("wrap")[0];
    var contentdom = document.getElementsByClassName("content")[0];

    if (wrapdom) {
      if (!this.hasClass(wrapdom, "wrapcontent"))
        wrapdom.className += " " + "wrapcontent";
      if (!this.hasClass(contentdom, "wrapcontent"))
        contentdom.className += " " + "wrapcontent";
    }

    this.initMap();
    this.getresourcelist();
    this.getinverterinfo();
    this.getbarstatis();
    this.getpiedata();
  },
  beforeDestroy() {
    //销毁前状态
    var wrapdom = document.getElementsByClassName("wrap")[0];
    var contentdom = document.getElementsByClassName("content")[0];
    if (this.hasClass(wrapdom, "wrapcontent")) {
      var reg = new RegExp("(\\s|^)" + "wrapcontent" + "(\\s|$)");
      wrapdom.className = wrapdom.className.replace(reg, " ");
    }

    if (this.hasClass(contentdom, "wrapcontent")) {
      var reg = new RegExp("(\\s|^)" + "wrapcontent" + "(\\s|$)");
      contentdom.className = contentdom.className.replace(reg, " ");
    }
  }
};
</script>
<style>
.pagebox {
  position: relative;
  font-size: 14px;
}
.mapbtnsbox {
  position: absolute;
  z-index: 2;
  right: 15px;
  bottom: 20px;
  width: 30px;
  height: 95px;
}
.backview {
  margin-bottom: 5px;
  background: #fff;
  font-size: 18px;
}
.zoom_btn {
  background: #fff;
  font-size: 18px;
}
.zoom_btn div,
.backview div {
  width: 30px;
  height: 30px;
  padding: 5px;
  box-sizing: border-box;
}
/**头部搜索框**/
.runsearchbox {
  width: 300px;
  height: auto;
  position: absolute;
  top: 4px;
