  line-height: 30px;
  margin-bottom: 10px;
}
.runsearchbox .searchrow .el-icon-search {
  font-size: 18px;
}
.resourcebox {
  background: #fff;
  padding: 0;
  box-sizing: border-box;
}
.resourcebox > .resourcetitle {
  margin-bottom: 3px;
  padding: 5px;
  box-sizing: border-box;
}
.resourcetitlebox .spanicon {
  margin-right: 3px;
  font-size: 12px;
  border-radius: 100%;
  padding: 4px;
  color: #fff;
  box-sizing: border-box;
}
.resourcebox .resourcetitle .titleright {
  cursor: pointer;
  font-size: 14px;
  float: right;
  text-align: right;
  color: #108cee;
}
.resourcebox .resourcepanel {
  border-bottom: 1px solid #ccc;
  padding: 5px;
  box-sizing: border-box;
}
.resourcebox .resourcepanel .el-col {
  padding: 10px;
  box-sizing: border-box;
  height: 72px;
}
.resourcebox .resourcepanel .el-col.active {
  border-bottom: 2px solid #108cee;
}
.resourcebox .resourcepanel .el-col.active .resourcename {
  color: #108cee;
}
.resourcebox .resourceitem {
  cursor: pointer;
}
.resourcebox .resourceimg span {
  font-size: 18px;
  padding: 8px;
  color: #fff;
  box-sizing: border-box;
}

.resourcebox .resourceimg {
  text-align: center;
}
.resourcebox .resourcename {
  text-align: center;
  margin-top: 5px;
  height: 20px;
  line-height: 20px;
}
.gf {
  border-radius: 100%;
  border: 1px solid #44db5e;
  background: #44db5e;
}
.fd {
  border-radius: 100%;
  border: 1px solid #ff9600;
  background: #ff9600;
}
.hd {
  border-radius: 100%;
  border: 1px solid #fe3824;
  background: #fe3824;
}
.sd {
  border-radius: 100%;
  border: 1px solid #108cee;
  background: #108cee;
}
.totalresourcebox {
  color: #108cee;
  cursor: pointer;
  width: 150px;
  padding: 5px;
  box-sizing: border-box;
}
.resourcelistbox {
  height: auto;
  padding-top: 3px;
  box-sizing: border-box;
}
.resourcelist {
  max-height: 270px;
  overflow: auto;
  padding: 5px 10px;
  padding-top: 0;
  box-sizing: border-box;
}
.resourceslide {
  height: 28px;
  line-height: 28px;
  color: #108cee;
  cursor: pointer;
  background: #eee;
  padding: 0 5px;
  box-sizing: border-box;
}
.ritem {
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.ritem + .ritem {
  border-top: 0;
  border-bottom: 1px solid #ccc;
}
.ritem > div {
  line-height: 25px;
}

.resourcetitlebox {
  line-height: 30px;
}
.resourcetitlebox .rightcol {
  text-align: right;
}
.resourcetitlebox .rightcol .updownicon {
  cursor: pointer;
}
/**右方图表**/
.slidecontentbox {
  position: absolute;
  right: 0;
  top: 0;
  width: 15px;
  z-index: 2;
  overflow: hidden;
}
.slidecontentbox.sliderspread {
  width: 315px;
  height: 100%;
}
.slidebar {
  margin-top: 5px;
  width: 15px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  color: #333;
  float: left;
  background: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}
.chartsbox {
  width: 299px;
  height: auto;
  max-height: 100%;
  overflow: auto;
  float: left;
  background: #fff;
}
.mapbtnsbox.sliderspread {
  right: 320px;
}

.chartsitem {
  background: #fff;
  border: 1px solid #ccc;
}
.chartsitem + .chartsitem {
  margin-top: 5px;
}
.chartsitem.nbbox {
  height: 80px;
  width: 100%;
}
.topblock {
  margin-top: 10px;
  line-height: 25px;
  color: #108cee;
  text-align: center;
}
.countspan {
  font-size: 16px;
}
.bottomblock {
  margin-top: 5px;
  line-height: 25px;
  text-align: center;
}
.chartsitem.cnbox {
  height: 240px;
  padding: 5px;
  box-sizing: border-box;
}
.itemchild + .itemchild {
  margin-top: 3px;
}
.spanblock {
  display: inline-block;
}
#cncharts {
  height: 120px;
}
.spanblock .el-date-editor.el-input {
  width: 135px;
}
.itemtitle {
  font-size: 15px;
  font-weight: bold;
  padding: 5px;
  box-sizing: border-box;
}
.cnbox .itemtitle {
  line-height: 34px;
  padding: 0;
}
.chartsitem.statusbox {
