}
.detailbtn {
  cursor: pointer;
  color: #108cee;
}
.poptitle [class^="iconm"] {
  color: #fff;
  font-size: 12px;
  padding: 4px;
  box-sizing: border-box;
}

#selectedpoint {
  width: 22px;
  height: 22px;
  margin-top: -10px;
  margin-left: -9px;
}
.iconm-loc2 {
  color: #fe3824;
  font-size: 22px;
}
.wrapcontent {
  padding: 0 !important;
  height: 100% !important;
}
</style>




